import { Breadcrumbs, Button, List, ListItem, ListItemText, Typography } from "@material-ui/core";
import { Link, navigate } from "gatsby";
import * as React from "react";
import Carousel from "react-material-ui-carousel";
import ajax, { useFetcher } from "shared/components/ajax";
import { useUserInfo } from "shared/components/save";
import { useModalTheme } from "shared/components/style";
import Defer from "shared/components/ui/Defer";
import Navbar from "shared/components/ui/Navbar";
import { expandImg, getSearchParam, rupiah, winconfirm } from "shared/components/util";

const Page = () => {
    const user = useUserInfo();
    const id = getSearchParam().get('id');
    const item = useFetcher('/market/discover/goods/' + id);
    const modalTheme = useModalTheme();
    const submit = () => {
        ajax.post('/market/cart/add', {
            good_id: id,
            qty: 1,
            note: '',
        }).then(x => {
            winconfirm('Berhasil. Lihat keranjang?').then(x => x && navigate('/user/cart'));
        }).catch(x => {
            alert(x.response?.data.message || x.message);
        });
    }
    return (
        <Defer data={user && item}>
            {() => <div>
                <Navbar />
                <List>
                    <ListItem>
                        <Breadcrumbs>
                            <Link color="inherit" to="/pasar/cari/">
                                Belanja
                            </Link>
                            <Link color="inherit" to={"/pasar/toko?id=" + item.data.store.id}>
                                {item.data.store.title}
                            </Link>
                            <Typography color="textPrimary">{item.data.title}</Typography>
                        </Breadcrumbs>
                    </ListItem>
                    <ListItem style={{ flexDirection: 'column' }}>
                        <Carousel>
                            {item.data.galleries.map(x => (
                                <div key={x.url} style={{ textAlign: 'center' }}>
                                    <img src={expandImg(x.url)} alt='' height="300px" />
                                </div>
                            ))}
                        </Carousel>
                    </ListItem>
                    <ListItem>
                        <ListItemText primary={item.data.title} secondary={rupiah(item.data.price)}
                            primaryTypographyProps={{ variant: 'h6' }} secondaryTypographyProps={{ variant: 'subtitle1' }} />
                    </ListItem>
                    <ListItem>
                        <Typography>
                            {item.data.desc}
                        </Typography>
                    </ListItem>
                    <ListItem>
                        <ListItemText primary={'Stok'} />
                        <ListItemText primary={item.data.stock} className={modalTheme.right} />
                    </ListItem>
                    <ListItem>
                        <ListItemText primary={'Dikirim Dari'} />
                        <ListItemText primary={item.data.store.address?.sector || '-'} className={modalTheme.right} />
                    </ListItem>

                    <ListItem>
                        <Button fullWidth color="primary" variant="contained"
                            style={{ marginLeft: 'auto' }} onClick={submit}>Tambahkan ke Keranjang</Button>
                    </ListItem>
                </List>

            </div>
            }
        </Defer>
    );
}

export default Page
